import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';
import AddIcon from '@material-ui/icons/Add';
import React, { useState } from 'react';
import "./CreateNote.css";

const CreateNote = (props) => {

  const[isExpanded, setIsExpanded] = useState(false);

   const [note, setNote] = useState({
    title: "",
    content: ""
   })

  const expand = () => {
    setIsExpanded(true);
  }

  const handleChange = (e) => {
    const {name, value} = e.target;
    setNote((prevNote) => {
      return {
        ...prevNote,
        [name]: value,
      };
    });
  };

  const submitNote = (e) => {
    if (note.title.trim().length === 0 || note.content.trim().length === 0) {
      alert('Enter Some Notes...');
    } else {
    e.preventDefault();
    props.addNote(note);
    setNote({
      title: "",
      content: ""
    });
    setIsExpanded(false);
  }
  };

  return (
    <div>
      <form className='create-note'>
        {isExpanded && <input type='text' name="title" placeholder='Title' onChange={handleChange} value={note.title} /> }
        <textarea name='content' placeholder='Take a Note...' onClick={expand} onChange={handleChange} rows={isExpanded ? 3 : 1} value={note.content} />
        <Zoom in={isExpanded}>
          <Fab onClick={submitNote}>
            <AddIcon />
          </Fab>
        </Zoom>
      </form>
    </div>
  )
}

export default CreateNote